import { AfterViewInit, Component, OnInit } from "@angular/core";
import { StorageService } from "src/app/shared/services/storage.service";
import { UserApiService } from "src/app/shared/services/user-api.service";
import { UserService } from "src/app/shared/services/user.service";
import { MainApiService } from "../../services/main-api.service";
import { MainArtsService } from "../../services/main-arts.service";

@Component({
  selector: "app-main",
  templateUrl: "./main.component.html",
  styleUrls: ["./main.component.scss"],
})
export class MainComponent implements OnInit, AfterViewInit {
  arts = [];
  pageCount = [];
  currentId = "";
  likes = 0;

  constructor(
    private _mainApiService: MainApiService,
    private _userApiService: UserApiService,
    private _mainArtService: MainArtsService,
    private _storageService: StorageService,
    private _userService: UserService
  ) {}

  ngOnInit() {
    this._init();
  }

  ngAfterViewInit() {}

  getDataForPage(page) {
    this.arts = [];
    this._storageService.setItem("page", page);
    this._mainApiService.getAllArts({ page }).subscribe((res) => {
      res.arts.forEach((e) => {
        this._userApiService.getUserInfo(e.owner).subscribe((user) => {
          this.arts.push({ ...e, email: user.resUser.email });
        });
      });
    });
  }

  editCard(key, value, id, index) {
    let data;
    if (key === "title") {
      data = { title: value };
    }
    if (key === "descr") {
      data = { descr: value };
    }
    console.log(data);
    this._mainApiService
      .editPost(id, this._storageService.getItem("id"), data)
      .subscribe((res) => {
        console.log(res);
      });
  }

  removePost(id, index) {
    console.log(id, this._storageService.getItem("id"));
    this._mainApiService
      .removePost({}, id, this._storageService.getItem("id"))
      .subscribe((res) => {
        if (!res.error) {
          this.arts.splice(index, 1);
        }
      });
  }

  likePost(id, index) {
    this._mainApiService
      .likePost(id, this._storageService.getItem("id"), {
        like: true,
      })
      .subscribe((res) => {
        console.log(res);
        if (!res.error) {
          // lik;
          this.arts[index] = res;
        }
      });
  }

  disPost(id, index) {
    this._mainApiService
      .likePost(id, this._storageService.getItem("id"), {
        like: "dis",
      })
      .subscribe((res) => {
        console.log(res);
        if (!res.error) {
          // lik;
          this.arts[index] = res;
        }
      });
  }

  _init(page?) {
    this.arts = [];
    if (!page) {
      if (this._storageService.getItem("page")) {
        page = this._storageService.getItem("page");
      }
    }
    this.currentId = this._storageService.getItem("id");
    page ? this._storageService.setItem("page", page) : null;
    this._mainApiService
      .getAllArts({ page: page ? page : 0 })
      .subscribe((res) => {
        this.pageCount = Array(Math.ceil(res.pages))
          .fill({})
          .map((x, i) => i);
        res.arts.forEach((e) => {
          this._userApiService.getUserInfo(e.owner).subscribe((user) => {
            this.arts.push({ ...e, email: user.resUser.email });
          });
        });
      });
  }
}
